<template>
  <el-row
    type="flex"
    justify="center"
    class="main-row"
    style="margin-top: 10px"
  >
    <el-col class="col">
      <el-row :gutter="20">
        <el-col :span="4">
          <div class="sideHead">
            <el-avatar :size="40" :src="src"></el-avatar>
            <div class="title hidden-xs-only">个人中心</div>
          </div>
          <el-divider></el-divider>
          <button @click="selfInfo" class="sideItem">
            <i class="el-icon-search"></i
            ><span class="hidden-xs-only">个人资料</span>
          </button>
          <button @click="selfOrder" class="sideItem">
            <i class="el-icon-sell"></i
            ><span class="hidden-xs-only">订单中心</span>
          </button>
          <button @click="selfPassenger" class="sideItem">
            <i class="el-icon-user"></i
            ><span class="hidden-xs-only">乘客信息</span>
          </button>
          <button @click="selfHelp" class="sideItem">
            <i class="el-icon-setting"></i
            ><span class="hidden-xs-only">设置</span>
          </button>
          <el-divider></el-divider>
        </el-col>
        <el-col :span="20">
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
import "element-ui/lib/theme-chalk/display.css";
export default {
  data() {
    return {
      src: "",
    };
  },
  methods: {
    selfInfo() {
      this.$router.push({
        name: "selfInfo",
      });
    },
    selfOrder() {
      this.$router.push({
        name: "selfOrder",
      });
    },
    selfPassenger() {
      this.$router.push({
        name: "selfPassenger",
      });
    },
    selfHelp() {
      this.$router.push({
        name: "selfHelp",
      });
    },
  },
  mounted() {
    this.src = this.$store.state.user.userForm.usrAvatar;
  },
};
</script>

<style scoped>
button {
  border: none;
  outline: none;
  background-color: #f8f8f8;
}
i {
  margin-right: 5px;
}
.col {
  width: 992px;
}
.sideHead {
  margin-top: 25px;
  display: flex;
  justify-content: center;
}
.sideHead .title {
  width: 60%;
  text-align: center;
  line-height: 40px;
  overflow: hidden;
}
.sideItem {
  position: relative;
  padding: 0 4px;
  width: 100%;
  height: 40px;
  text-align: left;
  transition: all 0.2s;
  line-height: 40px;
  font-size: 1em;
  border-radius: 5px;
}

.sideItem:hover {
  background-color: rgb(235, 233, 233);
}
.sideItem:focus {
  background-color: rgb(235, 233, 233);
  font-weight: 600;
}
.sideItem:focus i {
  font-weight: 600;
}
.sideItem:focus::before {
  position: absolute;
  content: "";
  background-color: #0750ec;
  width: 4px;
  height: 60%;
  left: -7px;
  top: 20%;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
</style>
